body {
  margin: 0;
  padding: 0;
  font-family: sans-serif;
  -webkit-font-smoothing: antialiased;
}

ul {
  list-style-type: none;
  margin: 0;
  padding: 0;
  overflow: hidden;
  background-color: #333;
}

li {
  display: inline;
  float: left;
}

button {
  border: none;
  display: block;
  background-color: #333;
  color: white;
  text-align: center;
  padding: 14px 16px;
  text-decoration: none;
}

button:hover {
  background-color: #111;
}

.center {
    margin: 0 auto;
    width: 75%;
}

textarea {
    width: 100%;
    overflow-y: scroll;
    height: 350px;
    resize: none;
}
